@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body{
    background-color: rgb(241, 228, 205);
    text-align: center;
    align-items: center;
    justify-content:center;
}

img{
    height: 100px;
    display:flex;
    margin: 100px  auto auto auto;
    
}

h1{
    font-family: 'Titillium Web', sans-serif;
    color: blue;
}
#sign-in-button{
    padding:10px;
    position:absolute;
    right: 0;
    left: 0;
    bottom: 50px;
    display:flex;
    margin-left: auto;
    margin-right: auto;
    background-image:linear-gradient(to bottom,rgb(151, 151, 250),blue);
    width: 150px;
    border-radius: 5px;
    border: 0;
    color: white;
    font-weight: 900;
    align-items: center;
    justify-content:center;
    font-size:15px;
}
#sign-in-button:active{
    background-image: linear-gradient(to top,rgb(151, 151, 250),blue);
}
body{
    background-color: rgb(241, 228, 205);
    text-align: center;
    align-items: center;
    justify-content:center;
}
span{
    display:flex;
    background-color:transparent;
    align-items:center;
    margin: 10px;
    cursor: pointer; 
}
span *{margin:10px;}
nav div{
    display:flex;
    margin-left: auto;
    margin-right:auto;
    background-color: transparent;
}
nav{
    display:flex;
    background-color:white;
    box-shadow: 0px 0px 3px 0px black;
    
    top: 0;
    right: 0;
    left: 0;
    z-index:2;
    align-items:center;
    justify-content: center;
}

#post{
    background-color:white;
    box-shadow:0px 0.5px  3px 0px black;
    position:fixed;
    right: 0 ; 
    left: 0;
    margin:0 auto;
    top: 80px;
    height:400px;   
    width:400px;
    z-index:2;
    display: none;
    border-radius:10px;
}

#close-button{
    border: 0;
    outline:none;
    background-color: transparent;
    visibility:hidden;
}

.user-image{
    border-radius:100px;
    height: 50px;
    width:50px;
}

#post-text{
    width: 320px;
    padding:5px;
    border: 0px solid black;
    border-radius:5px;
    outline:none;
    font-size:20px;
}

#gif-search{
    width: 250px;
    padding: 10px;
    margin-top: 10px;
    border-radius:5px;
    border: 1px solid black;
    transition: all 100ms ease-in-out;
    font-size: 16px;
}
#gif-search:focus{
transform:scaleX(1.08);
}

#gifs{
    width: 90%;
    overflow-x:auto;
    display:flex;
    margin-left: auto;
    margin-right: auto;
    
}
#gifs img{
    height:70px;
    width:70px;
    margin: 10px;
    border-radius:5px;
}

#gifs::-webkit-scrollbar{
    display:none;
    
}
#gif-preview{
    border-radius:10px;
    height:100px;
    width: 100px;
    position: absolute;
    top: 180px;
    left: 10px;
    margin-right: auto;
    
}

#post-button{
    border: 0px solid black;
    outline: none;
    position: absolute;
    border-radius: 50px;
    width: 100px;
    color: white;
    background-color:blue;
    font-weight: 900;
    bottom: 20px;
    right: 30px;
    padding: 10px;
}

#messages{
    border-radius: 10px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
    position: relative;
    height: 300px;
    width: 300px;
    background-color:white;

}

#messages-img{
height:150px;
width: 150px;
position: absolute;
bottom: 20px;
right: 0;
left: 0;
border-radius: 10px;
}
